$basicLinePadding: 8px;

body {
  margin: 0;
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  // default
  h1,
  h2,
  h1,
  h2,
  h3,
  h4,
  p {
    font-style: normal;
    font-weight: normal;
    letter-spacing: 0.01em;
    transition: 0.2s ease-out;
  }

  h1 {
    font-size: 40px;
    line-height: 40px + $basicLinePadding;
  }
  h2 {
    font-size: 35px;
    line-height: 35px + $basicLinePadding;
  }
  h3 {
    font-size: 25px;
    line-height: 25px + $basicLinePadding;
  }
  h4 {
    font-size: 20px;
    line-height: 25px + $basicLinePadding;
  }
  h5 {
    font-size: 18px;
    line-height: 18px + $basicLinePadding;
  }

  a {
    color: inherit;
    transition: 0.1s ease-out !important;
  }
  a:hover {
    color: inherit;
    text-decoration: none;
  }

  blockquote {
    display: block;
    font-family: "nyt-imperial", georgia, "times new roman", times, serif !important;
    font-size: 20px;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-left: 40px;
    margin-right: 40px;
  }

  .social-icon {
    margin-left: 5px;
    margin-right: 5px;
  }

  .section-title {
    margin-top: 2em;
    margin-bottom: 1.5em;
  }

  .lightText {
    color: $light-grey;
  }
  .darkText {
    color: $dark-grey;
  }
  .blueText {
    color: $munco-blue !important;
  }

  .darkText {
    color: $dark-grey !important;
  }

  .neutralText {
    color: $neutral-grey !important;
  }

  .miniText {
    font-size: 14px;
    line-height: 18px;
  }
  .weight-300 {
    font-weight: 300 !important;
  }
  .weight-400 {
    font-weight: 400 !important;
  }
  .weight-500 {
    font-weight: 500 !important;
  }
  .weight-700 {
    font-weight: 700 !important;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
